import {
	PARTNERCASHLIST
} from '../../../utils/api.js'
export default {
	data() {
		return {
			listData: '',
			list: ''
		}
	},
	filters: {
		typeFormate: function(value) {
			var result
			if (value == 1) {
				result = '普通邀请会员首次购买'
			} else if (value == 2) {
				result = '普通邀请会员续费购买'
			} else if (value == 3) {
				result = '合伙人邀请会员首次购买'
			} else if (value == 4) {
				result = '合伙人邀请会员续费购买'
			} else if (value == 5) {
				result = '合伙人提现'
			} else if (value == 6) {
				result = '合伙人提现失败'
			} else if (value == 7) {
				result = '普通收益提现'
			} else if (value == 8) {
				result = '普通收益提现失败'
			} else if (value == 9) {
				result = '卡密收益'
			}
			return result
		}
	},
	onLoad() {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			duration: 1000
		})
		this.initData()
	},
	methods: {
		initData() {
			const params = {}
			this.$request.url_request(PARTNERCASHLIST, params, 'GET', res => {
				this.listData = JSON.parse(res.data).data
				this.list = JSON.parse(res.data).data.records
				uni.hideToast()
			}, err => {})
		}
	}
}
